/***************************           Cards            *******************************/


.card {
  
    border-radius: 10px;
    background: transparent;
    grid-column: auto/ span 5;
    grid-row: 1;
    box-sizing: border-box;
    padding: 0px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.cardTitle {
    margin-top: 24px;
    font-family: 'SEATBCN-Bold';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #000000;

}

.cardDescription {
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #85837F;
    margin-top: 16px;
}
.imageContainer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.imageContainer > img {
    /* max-width: 271px; */
    
    object-fit: cover;
}



@media (max-width: 768px) {
    .card {
        grid-column: auto/ span 4;
        grid-column-gap: 20px;
        
        }
    
}

  @media (max-width: 360px) {
    .card {
        grid-column: auto/ span 3;
        grid-column-gap: 20px;
        
        }
    
}