.wrapperSecondaryNav{
    position: fixed;

    top: 88px;
    z-index: 4;
    
}
@keyframes mymove {
    from {opacity: 0%;}
    to {opacity: 1}
  }
.mainSecondary{

    height: auto;
    width: 100%;
    grid-area: 1 / 1 / span 1/ span 12;
    transform-origin: 0% 0%;
    transform: scaleX(1);
    animation-name: mymove;
    animation-duration: 1s;
}

@keyframes opacitybottom1 {
    from {background-color: black;}
    to {background-color: white;}
  }


@keyframes opacitybottom2 {
    from {background-color: black;}
    to {background-color: white;}
    
  }

@media only screen and (max-width: 768px) {
    .bottom1 {
        position: absolute;
        transform-origin: 0% 0%;
        transform: translate(-32px,-32px);
        width: calc(100% + 64px);
        height: 100vh;
        z-index: 3;
        
        animation: opacitybottom1 2000ms;
    
    }
    .bottom2 {
        position: absolute;
        transform-origin: 0% 0%;
        transform: translate(-32px,-32px);
        width: calc(100% + 64px);
        height: 100vh;
        z-index: 3;
        background-color: white;
        animation-name: opacitybottom2;
        animation-duration: 2000ms;
        
    
    }
    
    
    .wrapperSecondaryNav{
        position: absolute;
        padding: 0px 32px;
        border-style: none;
        border-width: 0px;
    }
    
    .mainSecondary{
        animation-name: none;
        padding: 32px 0px;
        height: auto;
        grid-area: 1 / 4 / span 1/ span 3;
    }
    
}
@media only screen and (max-width: 360px) {
    .wrapperSecondaryNav{
        top: 180px;   
      
    }
   
    .mainSecondary{
        height: auto;
        grid-area: 1 / 1 / span 1/ span 4;
    }
    
}
