.linkCart {
    text-decoration: none;
}
.cartContainer {
    
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
    
}
.svgCartContainer{
    position: relative;
    height: 28px;
    width: 28px;
    justify-content: center;
    align-items: center;
}
.cartText {
    text-decoration: none;
    margin: 0px 8px 0px 0px;
}
.nrCartText {
    margin: 6px 0px;
    height: 16px;
    width: 28px;
    position: absolute;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'SEATBCN-Bold';
    color: #FFFFFF;
}









@media only screen and (max-width: 768px) {
     
    
    

    .cartText{
        display: none;
    }

    


}
