.videoContainer {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;

    cursor: pointer;
    margin-top: 16px;
    display: flex;
    align-items: center;
    width: fit-content;
    justify-content: space-between;
    
}

.videoText {
    /* 16/Regular Title */
        display: flex;
        align-items: center;
        font-family: 'SEATBCN-Regular';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #FE3B1F;
        margin-right: 16px
    }