
.backgroundDiv {
    box-sizing: border-box;
    background-color: #000000;
    height: 88px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 32px;
    min-width: fit-content;
    width: 100%;
    transition: background-color 2s;
  }

.divLinks{
    width: fit-content;
    padding: 0px 16px;
    display: grid;
    grid: 20px / auto auto auto auto auto ;
    grid-column-gap: 32px;
}
.textDefaultNav{
    width: fit-content;
    font-family: 'SEATBCN-Regular';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
text-decoration: none;
height: 20px;
/* identical to box height, or 125% */

text-align: center;

/* Neutral/White 100 */

color: #FFFFFF; 
  }
.textDefaultNav:hover{
    color: #FE3B1F; 
}
.textSelectedNav {
    width: fit-content;
    font-family: 'SEATBCN-Regular';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
text-decoration: none;
height: 20px;
/* identical to box height, or 125% */

text-align: center;

/* Neutral/White 100 */

color: #FE3B1F; 
}

@media only screen and (min-width: 768px) {
    .backgroundDiv{
        background-color: transparent;
    }
    .backgroundDiv:hover{
        background-color: #000000;
    }
    .positionHeaderForDesktop{
        position: fixed;
        top: 0px;
        z-index: 1;
    }

}
@media only screen and (max-width: 768px) {
    .positionheader{
        position: fixed;
        top: 0px;
        z-index: 1;
    }
    .divLinks{
        
        display: none;
    }

}
