.wrapper{
    background-color: #000000;
    
    box-sizing: border-box;
    width: 100%;
}
.backgroundDiv {
    padding: 40px 32px 0px;
    display: grid;
    grid-template-columns: repeat(12,
						1fr);
   grid-template-rows: auto ;
    grid-column-gap: 32px;
    overflow: hidden;
  
}

.seatIcon {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-area: 1 / 1 / span 1 / span 2;
}
.explore {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-area: 1 / 5 / span 1 / span 2;
}
.legal {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-area: 1 / 7 / span 1 / span 2;
}
.support {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-area: 1 / 9 / span 1 / span 2;
    overflow: hidden;
    text-overflow: ellipsis;
}
.social {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-area: 1 / 11 / span 1 / span 2;
}
.firstLink {
    margin-top: 16px;
}
.footerSubtitle {
    font-family: 'SEATBCN-Bold';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-decoration: none;
    height: 20px;
    /* identical to box height, or 125% */
    text-overflow: ellipsis;
    text-align: center;

    /* Neutral/White 100 */

    color: #FFFFFF; 
}
.footerLinks {
    padding-top: 8px;
}
.footerText {
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-decoration: none;
    height: 20px;
    min-width: 1px;
    text-overflow: ellipsis;
    /* identical to box height, or 125% */

    text-align: center;

    /* Neutral/White 100 */

    color: #FFFFFF; 
}
.footerText:hover {
    color: #FE3B1F; 
}
.iconsWrapper {
    position: static;
}
.moIcons {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

}

@media  only screen and (max-width: 768px) {
    .backgroundDiv{
        
        grid-template-columns: repeat(6,
						1fr);
        grid-template-rows: auto auto auto;
        grid-row-gap: 40px;
    }
    .explore{
        grid-area: 2 / 1 / span 1 / span 3;
    }
    .legal{
        grid-area: 2 / 4 / span 1 / span 3;
    }
    .support{
        grid-area: 3 / 1 / span 1 / span 3;
    }
    .social{
        grid-area: 3 / 4 / span 1 / span 3;
    }
}

@media only screen and (max-width: 360px) {
    .backgroundDiv{
        padding: 40px 20px 0px;
        grid-template-columns: repeat(4,
						1fr);
        grid-template-rows: auto auto auto auto auto;
        grid-row-gap: 40px;
    }
    .explore{
        grid-area: 2 / 1 / span 1 / span 4;
    }
    .legal{
        grid-area: 3 / 1 / span 1 / span 4;
    }
    .support{
        grid-area: 4/ 1 / span 1 / span 4;
    }
    .social{
        grid-area: 5 / 1 / span 1 / span 4;
    }
}