.showBackToMenuBurguer {
    padding: 32px 20px;
    background-color: #000000;
    box-sizing: border-box;
    /* grid-area: 1 / 1 / span 1 / span 4; */
    display: flex;
    align-items: center;
    cursor: pointer;
    position: fixed;
    top: 88px;
    z-index: 5;
    width: 100vw;
}
.hideElement {
    display: none;
}

@media only screen and (min-width: 360px) {

    .showBackToMenuBurguer{
        display: none;
    }

}

.textBackToMenu{
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
/* identical to box height, or 125% */

display: flex;
align-items: center;
text-align: center;

/* Brand/Orange */

color: #FE3B1F;
}
.arrowIconBackToMenu {
    height: 28px;
    width: 28px;
    margin-right: 16px;

}