.wrapperCarrousel{
    background-color: #EFEBE6;
    box-sizing: border-box;
    padding: 0px 32px;
    --ncol: 12;
    --gapcol: 32px;
    --firstOperation: calc(var(--ncol) + 1);
    --secondOperation: calc(var(--gapcol) * var(--firstOperation));
    --thirdOperation: calc(100vw - var(--secondOperation));
    --widthOneColumn: calc(var(--thirdOperation) / var(--ncol));
    width: 100vw;
    display: grid;
    grid-template-columns:  repeat(auto-fill, --widthOneColumn); ;
    gap: 32px;
    grid-auto-flow: column;
    grid-auto-columns:  var(--widthOneColumn);
    overflow-x: scroll;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
}

.wrapperCarrousel::-webkit-scrollbar {
  
    display: none;
  }
.wrapperCarrousel::-webkit-scrollbar-track {
  background: transparent;
}

.wrapperCarrousel::-webkit-scrollbar-thumb {
  background: transparent;
  border: none;
}

@media (max-width: 768px) {
    .wrapperCarrousel {
        --ncol: 6;
    }
  }

  @media (max-width: 360px) {
    .wrapperCarrousel {
        --ncol: 4;
        --gapcol: 20px;
        gap: 20px;
        padding: 0px 20px;
    }
  }