.emptyDivWhenBurguerOpened {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    height: 88px;


}
.hideElement {
    display: none;
}
.wrapper {
    min-width: fit-content;
    width: 100vw;
    position: absolute;
    z-index: 5;
}
.wrapperWhenShowSecondaryHeader {
    position: fixed;
}


.wrapperBurguerMenuOptionSecondaryNav {
    width: 100vw;
    box-sizing: border-box;
    padding: 32px;
    background-color: #000000;
    display: grid;
    grid: 1fr / repeat(12, minmax(0, 1fr)); ;
    gap: 32px;
    grid-template-rows: auto;
    transform-origin: 0% 0%;
    transform: scaleY(1);
    opacity: 1;
    animation-name: openverticalwrapper;
    animation-duration: 1000ms;
}
.wrapperBurguerMenuOptionSecondaryNavClosing {
    opacity: 1;
    animation-name: closeverticalwrapper;
    animation-duration: 300ms;

}
@keyframes openverticalwrapper {
    from {opacity: 0;}
    to {opacity: 1;}

  }
@keyframes closeverticalwrapper {
    from {opacity: 1;}
    to {opacity: 0;}

  }
@keyframes openhorizontalwrapper {
    from {transform: translate(-100%,0%);}
    to {transform: translate(0%,0%);}

  }

@media only screen and (max-width: 768px) {
     
    
    .wrapperBurguerMenuOptionSecondaryNav {
        min-height: 100vh;
        animation-name: openhorizontalwrapper;
        transform-origin: 0% 0%;
        transform: translate(0%, 0%);
        animation-duration: 1s;
        background-color: transparent;
        padding: 0px;
        padding-right: 32px;
        border-left-style: solid;
        border-left-color: #000000;
        border-width: 32px;
        grid: 1fr / repeat(6, minmax(0, 1fr)); ;
    }

}
@media only screen and (max-width: 360px) {

    .wrapperBurguerMenuOptionSecondaryNav {
        background-color: #000000;
        grid: 1fr / repeat(4, minmax(0, 1fr)); 
        grid-column-gap: 20px;
        padding: 0px 20px;
        border-left-style:none;
        border-left-color: transparent;
        border-width: 0px;
    }
  } 


