.componentWrapper{
    padding: 184px 32px;
    background-color: #EFEBE6;
    row-gap: 0px;
}
 .primaryDescription {
    grid-area: 1 / 5 / span 1/ span 8;
    
 }
 .textPrimaryDescription{
    font-family: 'SEATBCN-Black';
    font-style: normal;
    font-weight: 750;
    font-size: 60px;
    line-height: 64px;
    color: #000000;

 }
 .regularSpanPrimaryDescription{
    color: #000000;
 }

.HighlightSpanPrimaryDescription{
    color: #FE3B1F
}

.primaryDescriptionButton{
    grid-area: 2 / 5 / span 1/ span 2;
    margin-top: 40px;
}

.secondaryDescription{
    grid-area: 1 / 1 / span 1/ span 3;
}
.secondaryDescriptionButton{
    margin-top: 24px;
}
.textSecondaryDescription{
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #000000;

 }
@media (max-width: 768px) {
    
    .secondaryDescription{
        margin-top: 64px;
        grid-area: 3 / 4 / span 1/ span 3;
    }
    .primaryDescriptionButton{
        grid-area: 2 / 1 / span 1/ span 2;
        margin-top: 40px;
    }
    .textPrimaryDescription{


        font-size: 48px;
        line-height: 52px;
    
     }
    .componentWrapper{
        padding: 144px 32px;
    }
    .primaryDescription {
        grid-area: 1 / 1 / span 1/ span 6;
    }
  }
  @media (max-width: 360px) {
      .textSecondaryDescription{
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
      }
    .secondaryDescription{
        grid-area: 3 / 2 / span 1/ span 3;
    }
    .primaryDescriptionButton{
        grid-area: 2 / 1 / span 1/ span 3;
        margin-top: 40px;
    }
    .textPrimaryDescription{


        font-size: 40px;
        line-height: 44px;
    
     }
    .componentWrapper{
        padding: 96px 20px;
    }
    .primaryDescription {
        grid-area: 1 / 1 / span 1/ span 4;
    }
  }