/***************************           Cards            *******************************/


.card {
  
    border-radius: 10px;
    background: #EFEBE6;
    grid-column: auto/ span 3;
    height: 312px;
    box-sizing: border-box;
    padding: 24px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.cardTitle {
    font-family: 'SEATBCN-Bold';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #000000;

}

.cardPrice {
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #85837F;
    margin-top: 8px;
}
.imageContainer{
    margin-top: 8px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.imageContainer > img {
    /* max-width: 271px; */
    
    object-fit: cover;
}
.horizontanLine {
    border-top: 1px solid #CBC4BC;
    height: 0px;
    width: 100%;
    margin-top: 23px;
    
}

.buyContainer {
    cursor: pointer;
    margin-top: 16px;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    
}

.buyText {
/* 16/Regular Title */
    display: flex;
    align-items: center;
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FE3B1F;
}

.line {
    background-color: #CBC4BC;

  }

  @media (max-width: 360px) {
    .card {
        grid-column: auto/ span 4;
        grid-column-gap: 20px;
        
        }
    
}