.wrapperBurguerOptions {
    display: grid;
}
.hideElement {
    display: none;
}
.burguerOptionsGridOpened {
    display: initial;
}
.burguerOptionsGridClosed {
    display: none
}
.burguerGridWrapper
{
    grid-area: 1 / 1 / span 1/ span 3;
}
.burguerOptionsOpened {
    overflow: hidden;

    grid-area: 1 / 1 / span 1/ span 3;
    box-sizing: border-box;
    background-color: #000000;
    height: 100vh;
    width: 100%;
    padding: 32px;
    padding-left: 0px;
  

}

.optionBurguerLink {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 32px;
    font-family: 'SEATBCN-Bold';
    font-style: normal;
    font-weight: 750;
    font-size: 30px;
    line-height: 32px;
    color: #FFFFFF; 
    text-decoration: none;
}


@media only screen and (max-width: 360px) {
    

    .burguerOptionsOpened {
        padding: 32px 0px;
        grid-area: 1 / 1 / span 1/ span 4;
    }
}

@media only screen and (min-width: 768px) {
    .wrapperBurguerOptions {
        display: none;
    }

    .burguerOptionsOpened {
        display: none;
        }
  
}