
.burguerIcon
{
    display: none;

}
.burguerIcon:active{
    background-image: none;
    outline: 0;
    box-shadow: none;
  }
.burguerIcon:focus{
    background-image: none;
    outline: 0;
    box-shadow: none;
  }
.burguerIcon:focus:active {
    background-image: none;
    outline: 0;
    box-shadow: none;
  }
@keyframes opacitybottom1 {
    from {opacity: 0;}
    to {opacity: 1}
  }

@media only screen and (max-width: 768px) {
    .burguerIcon
    {
        user-select: none;
        animation-name: opacitybottom1;
        animation-duration: 1s;
        opacity: 1;
        display: initial;
        cursor: pointer;
    }

}


