@keyframes opacitybottom1 {
    from {opacity: 0;}
    to {opacity: 1}
  }
.productGrid {
    
    display: grid;
    grid: 1fr / repeat(12, minmax(0, 1fr));
    gap: 32px;
}
@media only screen and (max-width: 768px) {
    .productGrid{
        position: absolute;
        z-index: 3;
        animation-name: opacitybottom1;
        animation-duration: 1300ms;
        grid: auto / repeat(3, minmax(0, 1fr));
    }
    
}
@media only screen and (max-width: 360px) {
    .productGrid{
        animation-name: opacitybottom1;
        animation-duration: 1300ms;
        grid: auto / repeat(4, minmax(0, 1fr));
    }
    
}